@charset "UTF-8";
@import url(../font/iconfont.css);
@import url(../font/material-icons.css);
blockquote {
  margin: 24px 0;
  padding-left: 24px;
  border-left: 8px solid #B4B4B4;
  background: #f5f5f5; }

p {
  padding: 12px 0; }

li {
  padding-top: 16px; }

img {
  display: block;
  width: auto;
  max-width: 100%;
  margin-bottom: 24px; }

/*------------代码--------------*/
pre {
  padding: 24px;
  background: #eceff1;
  border: 1px solid #9e9e9e;
  border-radius: 4px; }

code {
  padding-left: 8px;
  padding-right: 8px;
  color: #8d6e63;
  background: #eceff1;
  border-radius: 4px; }

/*------------代码 end --------------*/
/*------------表格--------------*/
table {
  border-left: 1px solid #9e9e9e;
  border-top: 1px solid #9e9e9e; }

th, td {
  padding: 12px;
  border-right: 1px solid #9e9e9e;
  border-bottom: 1px solid #9e9e9e; }

/*------------表格 end --------------*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #F0F0F0; }

.hljs, .hljs-subst {
  color: #444; }

.hljs-comment {
  color: #888888; }

.hljs-keyword, .hljs-attribute, .hljs-selector-tag, .hljs-meta-keyword, .hljs-doctag, .hljs-name {
  font-weight: bold; }

.hljs-type, .hljs-string, .hljs-number, .hljs-selector-id, .hljs-selector-class, .hljs-quote, .hljs-template-tag, .hljs-deletion {
  color: #880000; }

.hljs-title, .hljs-section {
  color: #880000;
  font-weight: bold; }

.hljs-regexp, .hljs-symbol, .hljs-variable, .hljs-template-variable, .hljs-link, .hljs-selector-attr, .hljs-selector-pseudo {
  color: #BC6060; }

.hljs-literal {
  color: #78A960; }

.hljs-built_in, .hljs-bullet, .hljs-code, .hljs-addition {
  color: #397300; }

.hljs-meta {
  color: #1f7199; }

.hljs-meta-string {
  color: #4d99bf; }

.hljs-emphasis {
  font-style: italic; }

.hljs-strong {
  font-weight: bold; }



